
.tab-item-content {
  height: 100%;
  overflow-y: scroll;
}
.node-active {
  color: #FB4F5B;
}
.tree-box {
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  height: 100.1%;
  padding-bottom: 2rem;
  box-sizing: border-box;
}
.tree-box-left,
.tree-box-right {
  height: 100%;
  overflow-y: scroll;
}
.tree-box-left {
  box-shadow: 0 0 5px #aaa;
}
.range-content .weui_cell_primary {
  background-color: #ccc;
  width: 4rem;
}
.page-list .content {
  padding-top: 4.4rem;
}
.template-content {
  height: 100%;
}
.category {
  width: 100%;
  height: 100%;
  padding-bottom: 4.7rem;
  box-sizing: border-box;
}
.list-search {
  position: static!important;
}
.category > .tree-list {
  position: absolute;
  width: 100%;
  top: 5rem;
  left: 0;
}
.bar.bar-header {
  position: relative;
}
.page-list .bar-header .vux-search-box {
  position: absolute!important;
  z-index: 10;
  top: 0;
  left: 0;
}
.page-list .vux-tab {
  position: absolute;
  top: 2.3rem;
  left: 0;
  width: 100%;
  z-index: 10;
}
.list > .goods-list {
  position: absolute;
  top: 2.2rem;
  left: 0;
}
.category-content {
  height: 100%;
  width: 100%;
}
.category-content .vux-slider {
  position: inherit;
}
.category-scene {
  padding-bottom: 2.5rem;
}
.node-title:active {
  color: #1ABC9C;
}



