
.user-logo .circle {
  overflow: hidden;
  width: 4rem;
  height: 4rem;
}
.bank-card {
  width: 100%;
  height: 160px;
  color: #fff;
}
.bank-name {
  padding-top: 20px;
}
.bank-code {
  padding-top: 16px;
}
.bank-PA {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#e55d67+0,e55774+55,e6517e+100 */
  background: #e55d67;
  /* Old browsers */
  /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, #e55d67 0%, #e55774 55%, #e6517e 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, #e55d67 0%, #e55774 55%, #e6517e 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#e55d67', endColorstr='#e6517e', GradientType=1);
  /* IE6-9 fallback on horizontal gradient */
}
.bank-IC {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#5184bc+0,3e60a8+100 */
  background: #5184bc;
  /* Old browsers */
  /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, #5184bc 0%, #3e60a8 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, #5184bc 0%, #3e60a8 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#5184bc', endColorstr='#3e60a8', GradientType=1);
  /* IE6-9 fallback on horizontal gradient */
}
.bank-BC {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#29af81+0,1c9db4+100 */
  background: #29af81;
  /* Old browsers */
  /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, #29af81 0%, #1c9db4 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, #29af81 0%, #1c9db4 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#29af81', endColorstr='#1c9db4', GradientType=1);
  /* IE6-9 fallback on horizontal gradient */
}



