
.tree {
  position: relative;
}
.isactive {
  background-color: #e4393c;
  color: #fff;
}
.node-item {
  position: absolute;
  top: 0;
  left: 5rem;
  z-index: 99;
}
.tree-leaf {
  width: 5rem;
}
li.tree-item {
  width: 5rem;
}
li.tree-root {
  width: 100%;
}
.search-box,
.search-box-item {
  box-sizing: border-box;
}



